.containerCharacteristicSlider{
    position: absolute;
    left: 1.5%;
    bottom: 13%;
    width: 265px;
    height: 130px;

    @media (max-width: 715px) {
        width: 198px;
        height: 81px;
    }

    @media (max-width: 515px) {
    left: 0;
    bottom: 0;
    height: 98px;
    width: 150px;
    }
 }

.windowNameStick{
    width: 217px;
    height: 130px;
    overflow: hidden;
    
    background-color: rgba(255, 255, 255, 0.72);

    @media (max-width: 715px) {
        width: 150px;
        height: 81px;
    }

    @media (max-width: 515px) {
        height: 60px;
    }
}

.allPagesContainer{
    height: 100%;
    display: grid;

    transition: translate;
    transition-property: transform;
    transition-duration: 300ms;
    transition-timing-function: ease-in-out;
}

.characteristicStick{
    padding: 32px 0 0 32px;
    width: 217px;
    height: 130px;

    @media (max-width: 715px) {
        padding: 16px 0 0 16px;
    }

    @media (max-width: 515px) {
        padding: 4px 0 0 4px;
    }
}

.numberAndType{
    margin-bottom: 8px;
    font-family: var(--font-primary);
    font-size: 1.6rem;
    font-weight: 500;
    line-height: 24px;
    text-align: left;
    color: #616161;

    @media (max-width: 715px) {
        font-size: 1.4rem;
        line-height: 20px;
    }
}

.name{
    font-family: var(--font-primary);
    font-size: 2.8rem;
    font-weight: 600;
    line-height: 34px;
    text-align: left;
    color: var(--secondary-font-color);

    @media (max-width: 715px) {
        font-size: 2.0rem;
        line-height: 26px;
    }
}

.buttonContainer{
    position: absolute;
    right: 0;
    bottom: 0;

    display: flex;
    justify-content: center;
    align-items: center;
}

.buttonSquare{
    width: 48px;
    height: 48px;
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    @media (max-width: 515px) {
        width: 38px;
        height: 38px;
    }
}