$bottomDotsHeight:25;

.sliderContainer {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
}

  // children

.windowSlider{
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.allPagesContainer{
    width: 100%;
    height: 100%;
    display: grid;

    transition: translate;
    transition-property: transform;
    // transition-duration: 300ms;
    transition-timing-function: ease-in-out;
    margin-bottom: calc(#{$bottomDotsHeight}px + 10px);
}

.buttonsContainer{
  position: absolute; 
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.buttonContainerLeft{
    position: relative;
    display: flex;
    z-index: 200;
} 

.buttonContainerRight{
  position: relative;
  display: flex;
  z-index: 200;
} 

.buttonCircleLeft {
  position: absolute;
  width: 48px;
  height: 48px;
  cursor: pointer;

  top: 45%;
  left: 49%;
  transform: translate(-50%, -50%);
}

.buttonCircleRight {
    position: absolute;
    width: 48px;
    height: 48px;
    cursor: pointer;

    top: 45%;
    left: 51%;
    transform: translate(-50%, -50%);
}

    // dots

.dotsContainer {
    position: absolute;
    bottom: 5%;
    left: 50%;

    max-width: max-content;
    min-width: min-content;
    display: flex;
    justify-content: center;
  }

  .dots {
    position: relative;
    width: 27px;
    height: 27px;
    margin-right: 20px;
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

  }

  .activeDot{
    position: absolute;
  }
